window.addEventListener('DOMContentLoaded', () => {
    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
    })
    $('input[type="tel"]').mask('+9 999 999-9999', { autoclear: false }, { placeholder: '+1             ' });

})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

$(".review-card__link").click(function () {
    $(this).parents(".review-card").toggleClass("_all")
})
